<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>背景说明</span>
      </div>
      <div class="text item">
        <p>目前集客微服务平台、资金管理、订单系统等调用的三方系统接口包含CRM接口、无纸化接口等。</p>
        <p>举个例子，如果CRM接口系统无法调用，就会导致开发不能进行或测试环境的测试不能进行。同时由于内网的要求，部署在公司内网的测试环境应用无法访问局方三方系统的接口，存在挡板测试的情况。</p>
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>如何模拟三方接口服务</span>
      </div>
      <div class="text item">
        <p>采用 SpringBoot 开发对应三方系统的Rest接口，具体的上面已经提到，如`/OAuth/restOauth2Server/access_token` ， `/openapi/httpService/` ;</p>
        <p>模拟报文采用Mysql数据库新建表进行持久话存储，方便修改;</p>
        <p>以`method+入参`作为参数请求对应的Rest接口，服务通过参数查询数据库返回对应的模拟报文;</p>
        <p>模拟报文通过目前系统已有的日志记录表中获取，后续考虑提供导入、修改等功能。</p>
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>如何模拟三方接口服务</span>
      </div>
      <div class="text item">
        <p>采用 SpringBoot 开发对应三方系统的Rest接口，具体的上面已经提到，如`/OAuth/restOauth2Server/access_token` ， `/openapi/httpService/` ;</p>
        <p>模拟报文采用Mysql数据库新建表进行持久话存储，方便修改;</p>
        <p>以`method+入参`作为参数请求对应的Rest接口，服务通过参数查询数据库返回对应的模拟报文;</p>
        <p>模拟报文通过目前系统已有的日志记录表中获取，后续考虑提供导入、修改等功能。</p>
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>模拟三方接口服务流程</span>
      </div>
      <div class="text item">
        <p>正常流程如下：</p>
        <img src="static/images/1.png" />
        <p>通过模拟服务器流程如下：</p>
        <img src="static/images/2.png" />
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'intro',
  data() {
    return {
      msg: 'Welcome to mockWeb',
      activeName: "first"
    }
  }, methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    }
  }
}
</script>



